@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
body{margin:0;font-family:"Barlow Semi Condensed",-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:radial-gradient(#1f3756, #141539);overflow:hidden}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.App{height:100vh;width:100%;display:flex;flex-direction:column}
button{overflow:visible}button:disabled{cursor:default}button .pick-button{z-index:2;height:200px;width:200px;border-radius:50%;display:flex;align-items:center;justify-content:center}button .pick-button--rock{background:linear-gradient(90deg, #dc2e4e, #dd405d);box-shadow:0px 8px 0px 0px #861328;-webkit-box-shadow:0px 8px 0px 0px #861328;-moz-box-shadow:0px 8px 0px 0px #861328}button .pick-button--paper{background:linear-gradient(90deg, #4865f4, #5671f5);box-shadow:0px 8px 0px 0px #243dbc;-webkit-box-shadow:0px 8px 0px 0px #243dbc;-moz-box-shadow:0px 8px 0px 0px #243dbc}button .pick-button--scissors{background:linear-gradient(90deg, #ec9e0e, #eca922);box-shadow:0px 8px 0px 0px #a26a02;-webkit-box-shadow:0px 8px 0px 0px #a26a02;-moz-box-shadow:0px 8px 0px 0px #a26a02}button .pick-button--sm{height:136px;width:136px}button .pick-button__icon{width:72%;height:72%;background:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;box-shadow:inset 0 8px 0 #00000023}button .circle{width:200px;height:200px;background:#fff;opacity:.05;position:absolute;top:0;border-radius:50%;z-index:-2;pointer-events:none;transition:.5s all ease-in-out}button .expand-sm{transform:scale(1.5)}button .expand-md{transform:scale(2)}button .expand-lg{transform:scale(2.5)}
.result{z-index:2;transition:.5s all ease-in-out}.result--hide{opacity:0;pointer-events:none}.result__title{font-size:3rem;font-weight:700;color:#fff;margin-bottom:1rem}.result button{background:#fff;color:#3b4363;font-weight:700;border-radius:.5rem;padding:1rem 1rem;width:100%}@media(max-width: 768px){.result{transform:translate3d(0, 220px, 0) scale(1.8)}}
.arena{height:100%;display:flex;align-items:center;justify-content:center;position:relative}.arena__picker{z-index:1;position:absolute;transition:.5s all ease-in-out}.arena__picker--choose-rock{transform:translate3d(0, 156px, 0)}.arena__picker--choose-paper{transform:translate3d(-156px, -156px, 0)}.arena__picker--choose-scissors{transform:translate3d(156px, -156px, 0)}.arena__picker--hide{opacity:0}.arena__picker--player-choice{transform:translate3d(-320px, 0, 0) scale(1.4)}.arena__picker--house-choice{transform:translate3d(320px, 0, 0) scale(1.4)}.arena__picker--loser{transform:scale(1.2)}.arena .players-choice{display:flex;justify-content:space-between;position:absolute;width:760px;transform:translate3d(0, -220px, 0);color:#fff;font-size:1.75rem;transition:.5s all ease-in-out}.arena .players-choice .house{transform:translate3d(50px, 0, 0)}.arena .players-choice--hide{opacity:0}.arena__triangle{position:absolute;transition:.5s all ease-in-out}.arena__triangle--hide{opacity:0}@media(max-width: 768px){.arena{transform:scale(0.6);height:50%}.arena__picker--player-choice{transform:translate3d(-156px, 0, 0) scale(1.2)}.arena__picker--house-choice{transform:translate3d(156px, 0, 0) scale(1.2)}.arena .players-choice{width:100%;transform:translate3d(0, -220px, 0)}.arena .players-choice .player{transform:translate3d(-30%, 0, 0)}.arena .players-choice .house{transform:translate3d(30%, 0, 0)}}
footer{position:fixed;bottom:1rem;padding:0 2rem;display:flex;justify-content:flex-end;width:100%;box-sizing:border-box}footer button{border:1px solid #fff;color:#fff;font-size:1.2rem;font-weight:600;padding:.75rem 2rem;border-radius:.5rem}@media(max-width: 768px){footer{justify-content:center}}
.scoreboard{background:#fff;border-radius:.5rem;display:flex;align-items:center;justify-content:center;flex-direction:column;padding:1rem;min-width:116px}.scoreboard__title{font-size:1rem;letter-spacing:.2rem;line-height:1.2rem}.scoreboard__points{font-size:4rem;line-height:4rem}@media(max-width: 768px){.scoreboard{min-width:64px}.scoreboard__title{font-size:.8rem;letter-spacing:.1rem}.scoreboard__points{font-size:3rem;line-height:3rem}}
.header{display:flex;align-items:center;justify-content:space-between;border:2px solid #fff;padding:1rem;border-radius:1rem;width:720px;margin:2rem auto 0 auto}@media(max-width: 768px){.header{margin:1rem 1rem 0 1rem;width:auto}}
.rules{position:fixed;height:100vh;width:100%;display:flex;align-items:center;justify-content:center;top:0;z-index:99}.rules__container{width:480px;height:480px;background:#fff;position:absolute;border-radius:.5rem;flex-direction:column;display:flex}.rules__container__title{color:#3b4363;text-transform:uppercase;font-size:2rem;display:flex;justify-content:space-between;align-items:center;padding:2rem 2rem 0 2rem}.rules__container__illustration{display:flex;align-items:center;justify-content:center;height:100%}.rules__container>.icon-close{display:none}.rules__dark-overlay{background:#00000055;width:100%;height:100%}@media(max-width: 768px){.rules__container{width:100%;height:100vh;border-radius:0;display:flex;justify-content:center;align-items:center}.rules__container__title{font-size:3rem;margin-bottom:4rem}.rules__container__title .icon-close{display:none}.rules__container__illustration{height:auto;height:initial}.rules__container>.icon-close{display:block;margin-top:5rem}}
button{background:none;border:none;outline:none;cursor:pointer}
